.main-navigation {
  margin-top: 3rem;
  margin-bottom: 3rem; }
  @media only screen and (min-width: 1080px) {
    .main-navigation {
      text-align: right; } }
  .main-navigation .menu {
    display: inline; }
  .main-navigation ul {
    margin-bottom: 0; }
    @media only screen and (min-width: 600px) {
      .main-navigation ul {
        display: inline-block; } }
  .main-navigation li {
    display: inline-block; }
    .main-navigation li:not(:last-child):after {
      letter-spacing: 1rem;
      content: ' |'; }
    @media only screen and (min-width: 600px) {
      .main-navigation li:after {
        letter-spacing: 1rem;
        content: ' |'; } }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: .875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

